import { Button, Modal } from 'antd';
import { post_api } from "../../../api/api";

function Modal_form(props) {
    function updatePost(id) {
        post_api.updatePost(id, props.update_name, props.update_value, props.update_units).then((result) => {
            post_api.getPosts().then((data) => props.set_formuls_list(data.data))
            props.setIsModalOpen(false);
        })
    }

    return (
        <>
            <Modal title="Basic Modal" open={props.isModalOpen} onOk={props.handleOk} onCancel={props.handleCancel}>
                <p><label htmlFor="">name</label><input type="text" value={props.update_name} onChange={(e) => props.set_update_name(e.currentTarget.value)} /></p>
                <p><label htmlFor="">value</label><input type="text" value={props.update_value} onChange={(e) => props.set_update_value(e.currentTarget.value)} /></p>
                <p><label htmlFor="">units</label><input type="text" value={props.update_units} onChange={(e) => props.set_update_units(e.currentTarget.value)} /></p>
                <Button type="primary" onClick={() => updatePost(props.el.id)}>
                    add
                </Button>
            </Modal>
        </>
    )
}
export default Modal_form;