import axios from "axios";

export const instanse = axios.create({
    baseURL: "https://andrey546.pythonanywhere.com/api/",
    headers: { "Content-Type": "application/json", accept: 'application/json' },
})

export const post_api = {
    async getPosts() {
        let response = await (instanse.get("posts"))
        return response
    },
    async getPost(id) {
        let response = await (instanse.get(`posts/${id}`))
        return response
    },
    async deletePost(id) {
        let response = await (instanse.delete(`posts/${id}`))
        return response
    },
    async updatePost(id, name, value, units) {
        let response = await (instanse.patch(`posts/${id}/`, { name, value, units, owner: 1 }))
        return response
    },
    async addPost(name, value, units) {
        let response = await (instanse.post("posts/", { name, value, units, owner: 1 }))
        return response
    }
}