import { useEffect, useState } from 'react';
import './formuls.css';
import { post_api } from '../../api/api';
import Form from './form/form';
import Formul from './formul/formul';

function Formuls(props) {
    useEffect(() => {
        post_api.getPosts().then((data) => set_formuls_list(data.data))
    }, [])



    let [formuls_list, set_formuls_list] = useState([])
    return (
        <div className='formuls_panel'>
            <div className="formuls_title">
                <h2>формулы</h2>
            </div>

            <div className="formuls_7">
                <p>{formuls_list.map((el) => <Formul el={el} set_formuls_list={set_formuls_list} status={props.status} />)}
                </p>
            </div>
            {props.status && <Form set_formuls_list={set_formuls_list} />
            }
        </div>
    )
}
export default Formuls;