import logo from './logo.svg';
import './App.css';
import { useState } from 'react';
import Formuls from './components/formuls/formuls.jsx';
import { Button, Modal } from 'antd';

function App() {
  let [status, set_status] = useState(false)
  let [password, set_password] = useState("")
  const [isModalOpen, setIsModalOpen] = useState(false);
  let check_password = (e) => {
    set_password(e.currentTarget.value)
    if (password == 102938) {
      set_status(true)
      setIsModalOpen(false);
    }
  }
  const showModal = () => {
    setIsModalOpen(true);
  };
  const handleOk = () => {
    setIsModalOpen(false);
  };
  const handleCancel = () => {
    setIsModalOpen(false);
  };
  return (
    <div className="App">
      <button className='change_button' onClick={showModal}>Login</button>
      {/* <button className='change_button' onClick={() => set_status(!status)}>change</button> */}
      {/* <Button type="primary" defaultColor="black" onClick={showModal}>Login</Button> */}
      <Modal title="Basic Modal" open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>
        <input value={password} type="password" onChange={(e) => check_password(e)} />
      </Modal>
      <Formuls status={status} />
    </div>
  )
}

export default App;
