import { useState } from "react"
import { post_api } from "../../../api/api"
import { useForm } from "react-hook-form"


function Form(props) {
    let [name, set_name] = useState("")
    let [value, set_value] = useState("")
    let [units, set_units] = useState("")
    function addPost() {
        post_api.addPost(name, value, units).then((result) => {
            set_name("")
            set_value("")
            set_units("")
            post_api.getPosts().then((data) => props.set_formuls_list(data.data))
        })
    }

    const {
        register,
        handleSubmit,
        watch,
        formState: { errors },
    } = useForm()
    const onSubmit = (data) => console.log(data)
    return (
        <>
            <form onSubmit={handleSubmit(onSubmit)}>
                <input defaultValue="test" {...register("example")} />

                {/* include validation with required or other standard HTML validation rules */}
                <input {...register("exampleRequired", { required: true })} />
                {/* errors will return when field validation fails  */}
                {errors.exampleRequired && <span>This field is required</span>}

                <input type="submit" />
            </form>
            <div className='form'>
                <p><label htmlFor="">name</label><input type="text" value={name} onChange={(e) => set_name(e.currentTarget.value)} /></p>
                <p><label htmlFor="">value</label><input type="text" value={value} onChange={(e) => set_value(e.currentTarget.value)} /></p>
                <p><label htmlFor="">units</label><input type="text" value={units} onChange={(e) => set_units(e.currentTarget.value)} /></p>
                <button onClick={addPost}>add</button>
            </div>
        </>
    )

}
export default Form