import { Button } from "antd"
import Modal_form from "../modal_form/modal_form"
import { useState } from "react";
import { post_api } from "../../../api/api";

function Formul({ el, set_formuls_list, status }) {
    function deletePost(id) {
        post_api.deletePost(id).then((result) => {
            post_api.getPosts().then((data) => set_formuls_list(data.data))
        })
    }
    const [isModalOpen, setIsModalOpen] = useState(false);
    const showModal = (id) => {
        post_api.getPost(id).then((data) => {
            set_update_name(data.data.name)
            set_update_value(data.data.value)
            set_update_units(data.data.units)
            setIsModalOpen(true);
        })
    };
    const handleOk = () => {
        setIsModalOpen(false);
    };
    const handleCancel = () => {
        setIsModalOpen(false);
    };
    let [update_name, set_update_name] = useState("")
    let [update_value, set_update_value] = useState("")
    let [update_units, set_update_units] = useState("")
    return (
        <div key={el.id}>
            <p className='formul'>{el.name}</p>
            <details>{el.value}<p>{el.units}</p></details>
            {status && <Button type="primary" onClick={() => deletePost(el.id)}>delete</Button>}
            {status && <>
                <Button type="primary" onClick={() => showModal(el.id)}>
                    Open Modal
                </Button>
                <Modal_form el={el} isModalOpen={isModalOpen} setIsModalOpen={setIsModalOpen} handleOk={handleOk} handleCancel={handleCancel} set_formuls_list={set_formuls_list}
                    set_update_name={set_update_name} set_update_value={set_update_value} set_update_units={set_update_units}
                    update_name={update_name} update_value={update_value} update_units={update_units} />
            </>}
        </div>
    )
}
export default Formul